@use '../abstracts/index' as *;

#vzorkovnik {
    .plus-gallery-wrap {
        margin-bottom: 32px !important;
        .plus-gallery-item {
            height: auto;
            .plus-gallery-item-img {
                border-radius: 8px;
                overflow: hidden;

                &:hover {
                    img {
                        transform: scale(1.05);
                    }
                }

                img {
                    transition: transform 0.3s ease-in-out;
                }
            }

            .plus-gallery-item-title {
                text-align: left;
                margin-top: 8px;
                font-weight: $fw-medium;
            }
        }
    }
}

.vzorkovnik-link {
    text-decoration: underline;
    font-weight: $fw-medium;
    max-width: 220px;
}

.product-note {
    label {
        font-size: 16px;
    }
    small {
        font-size: 14px;
        margin-bottom: 12px;
        margin-top: 8px;
    }
}

#productNote {
    height: 140px;
    resize: vertical;

    @include mq-max(s) {
        height: 90px;
    }
}
